<template>
  <div class="m-menu">
    <dl class="nav" @mouseleave="mouseleave">
      <dt></dt>
      <dd
        v-for="(item, index) in menuList"
        :key="index"
        @mouseenter="mouseenter(item)"
      >
        <i :class="item.icon"></i>
        {{ item.title }}
        <span class="arrow"></span>
      </dd>
    </dl>
    <div
      class="detail"
      v-if="curDetail"
      @mouseenter="detailEnter"
      @mouseleave="detailLeave"
    >
      <template v-for="(item, index) in curDetail.children">
        <h4 :key="index">{{ item.title }}</h4>
        <span v-for="(v, i) in item.children" :key="v + '_' + i">{{ v }}</span>
      </template>
    </div>
  </div>
</template>
<script>
import api from "@/api/index.js";
export default {
  data() {
    return {
      curDetail: null,
      menuList: [
        {
          title: "",
          icon: "food",
          children: [
            {
              title: "",
              children: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
              ],
            },
          ],
        },
        {
          title: "",
          icon: "takeout",
          children: [
            {
              title: "",
              children: [""],
            },
          ],
        },
        {
          title: "",
          icon: "hotel",
          children: [
            {
              title: "",
              children: ["", "/", "/", ""],
            },
          ],
        },
      ],
    };
  },
  created() {
    api.getMenuList().then(res => {
      if (res.status == 200) {
        this.menuList = res.data.data;
      }
    });
  },
  methods: {
    mouseenter(item) {
      this.curDetail = item;
    },
    mouseleave() {
      let self = this;
      this.timer = setTimeout(() => {
        this.curDetail = null;
      }, 200);
    },
    detailEnter() {
      clearTimeout(this.timer);
    },
    detailLeave() {
      this.curDetail = null;
    },
  },
};
</script>


