</template>
<script>
import Province from '@/components/changeCity/province.vue'
import hot from "@/components/changecity/hot.vue"
import Categroy from '@/components/changeCity/categroy.vue'
export default {
    data(){
        return{
            hotList: ["", "", "", "","","","","",""],
            rencentList: ["", "", "", ""],
        }
    },
    components:{
        Province,
        Categroy,
        hot
    }
}
</script>


